<template>
  <div class="home" v-if="player.name != ''">
    <div class="menu">
      <div id="nav">
        <p>-----菜单-----</p>
        <a href="javascript:void(0);" @click="gym">修炼</a> |
        <a href="javascript:void(0);" @click="upgrade">突破</a> |
        <a href="javascript:void(0);" @click="get_data" class="ban_button">历练</a> |
        <a href="javascript:void(0);" @click="rest">休息</a> |
        <br />
        <a href="javascript:void(0);" @click="get_data" class="ban_button">丹药</a> |
        <a href="javascript:void(0);" @click="get_data" class="ban_button">装备</a> |
        <a href="javascript:void(0);" @click="get_data" class="ban_button">炼器</a> |
        <a href="javascript:void(0);" @click="get_data" class="ban_button">炼丹</a> |
        <br />
        <p>-----信息-----</p>
      </div>
      <div class="info">
        <p>角色名: {{ player.name }}</p>
        <p>灵力: {{ player.exp }}</p>
        <p>生命值: {{ player.hp }}/{{ player.max_hp }}</p>
        <p>境界: {{ player.level }}</p>
        <p>寿命: {{ player.life }}/{{ player.max_life }}</p>
      </div>
      <div class="main">
        <div class="main_child" v-for="(word, key) in nowturn_msg" :key="key">{{ word }}</div>
      </div>
      <div class="map">
        <p>-----地图-----</p>
        <a href="javascript:void(0);" @click="get_data" class="ban_button">天星商会</a> |
        <a href="javascript:void(0);" @click="get_data" class="ban_button">精武堂</a> |
        <a href="javascript:void(0);" @click="adventure('慕兰草原')"
          >慕兰草原</a
        >
        |
        <a href="javascript:void(0);" @click="adventure('乱星海')">乱星海</a> |
        <br />
        <a href="javascript:void(0);" @click="adventure('坠龙荒野')"
          >坠龙荒野</a
        >
        |
        <a href="javascript:void(0);" @click="adventure('风暴峡湾')"
          >风暴峡湾</a
        >
        |
        <a href="javascript:void(0);" @click="adventure('北极冰宫')"
          >北极冰宫</a
        >
        | <a href="javascript:void(0);" @click="get_data" class="ban_button">飞升仙界</a> |
      </div>
    </div>
    <div class="tips">
      <p>提示：每一个行为都会经历1天，请把控好你的行为</p>
    </div>
    <div class="system_menu">
      <a href="javascript:void(0);" @click="get_data" class="ban_button">保存游戏</a> |
      <a href="javascript:void(0);" @click="get_data" class="ban_button">加载存档</a> |
    </div>
  </div>
  <div v-else>
    <input v-model="input_name"> <button @click="input_namefunc">确定名字</button>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      player: {
        name: "",
        exp: 0,
        level: "炼气境",
        hp: 100,
        max_hp: 100,
        life: 0,
        max_life: 100,
      },
      input_name: '',
      nowturn_msg: ["欢迎来到诸天大陆v0.0.1" , "恭喜你降世了！天生有灵根，可以修炼成仙！"],
      post_data: {},
      exp_config: {},
      level_config: {},
      adventure_config: {},
      event_config: {},
    };
  },
  created() {
    this.get_data();
  },
  watch: {
    name: function (val) {
      if(this.player.name == ''){
        this.input_namefunc();
      }
    },
  },
  methods: {
    input_namefunc(){
      this.player.name = this.input_name;
    },
    get_data() {
      this.$axios.get("./exp.json").then((data) => {
        this.exp_config = data.data;
      });
      this.$axios.get("./level.json").then((data) => {
        this.level_config = data.data;
      });
      this.$axios.get("./adventure.json").then((data) => {
        this.adventure_config = data.data;
      });
      this.$axios.get("./event.json").then((data) => {
        this.event_config = data.data;
      });
    },
    check_msggroup(){
      if(this.nowturn_msg.length >= 15){
        this.nowturn_msg.shift();
      };
    },
    turns_gone() {
      // 回合判断
      if (this.life_estimate(this.player.life, this.player.max_life)) {
        return true;
      } else {
        return false;
      }
    },
    life_estimate(life, max_life) {
      // 寿命判断
      if (life <= max_life) {
        return true;
      } else {
        return false;
      }
    },
    life_gone() {
      // 消耗寿命
      this.player.life += 1;
    },
    event_msg(behavior){
      console.log(behavior);
      let temp_msg_group = new Array();
      for (var eventflag in this.event_config) {
        if(behavior == this.event_config[eventflag].behavior){
          temp_msg_group.push(this.event_config[eventflag].msg);
        }
      };
      let random_num = Math.floor(Math.random()*temp_msg_group.length);
      console.log(random_num);
      let show_msg = temp_msg_group[random_num];
      console.log(show_msg);
      this.nowturn_msg.push(show_msg);
      this.check_msggroup();
    },
    gym() {
      // 修炼方法
      // 回合判断
      if (this.turns_gone()) {
        // 消耗寿命
        this.life_gone();
        // 获取天数经验配置
        let get_exp = 0;
        for (var expflag in this.exp_config) {
          if (this.player.level == this.exp_config[expflag].level) {
            if (eval(this.exp_config[expflag].days)) {
              get_exp = eval(this.exp_config[expflag].exp);
            }
          }
        }
        // 获得经验
        this.player.exp += get_exp;
        this.event_msg('gym');
        let exp_msg = "你获得了" + get_exp + "点修为";
        this.nowturn_msg.push(exp_msg);
        this.check_msggroup();
      }
    },
    upgrade() {
      // 突破方法
      // 回合判断
      if (this.turns_gone()) {
        // 消耗寿命
        this.life_gone();
        // 获取升级经验配置
        // 判断升级
        for (var levelflag in this.level_config) {
          if (this.player.level == this.level_config[levelflag].name) {
            if (eval(this.level_config[levelflag].nextlevel_exp)) {
              // 满足升级条件
              this.player.level = this.level_config[levelflag].next_level;
              eval(this.level_config[levelflag].nextlevel_max_life);
              this.player.max_hp = this.player.max_hp * 2;
              this.player.hp = this.player.max_hp;
              this.event_msg('upgrade_success');
              let next_level_msg = "恭喜你突破到" + this.level_config[levelflag].next_level + "!";
              this.nowturn_msg.push(next_level_msg);
              this.check_msggroup();
              break;
            }
            else{
              this.event_msg('upgrade_fail');
              break;
            }
          }
        }
      }
    },
    adventure(id) {
      // 冒险方法
      // 回合判断
      if (this.turns_gone()) {
        // 消耗寿命
        this.life_gone();
        // 获取配置
        // 扣血判断
        for (var adventureflag in this.adventure_config) {
          if (this.adventure_config[adventureflag].id == id) {
            if (eval(this.adventure_config[adventureflag].need_level)) {
              // 如果等级够
              if (eval(this.adventure_config[adventureflag].need_hp)) {
                // 如果血量够
                let flag = Math.round(Math.random());
                if (flag == 0) {
                  this.player.hp = Math.floor(
                    Math.random() * this.player.hp + 1
                  );
                }
                let temp_exp = this.adventure_config[adventureflag].exp_reward;
                this.player.exp += temp_exp;
                let behavior_flag = this.adventure_config[adventureflag].behavior_success
                this.event_msg(behavior_flag);
                let exp_msg = "你获得了" + temp_exp + "点修为";
                this.nowturn_msg.push(exp_msg);
                this.check_msggroup();
                // 奖励判断
              } else {
                // 如果血量不够
                this.player.hp = Math.floor(Math.random() * this.player.hp + 1);
                let temp_exp = Math.floor(
                  Math.random() *
                    this.adventure_config[adventureflag].exp_reward *
                    0.2 +
                    1
                );
                this.player.exp += temp_exp
                let behavior_flag = this.adventure_config[adventureflag].behavior_fail
                this.event_msg(behavior_flag);
                let exp_msg = "你获得了" + temp_exp + "点修为";
                this.nowturn_msg.push(exp_msg);
                this.check_msggroup();
              }
            } else {
              // 直接打不过
              this.player.hp = 1;
              let behavior_flag = this.adventure_config[adventureflag].behavior_fail
              this.event_msg(behavior_flag);
            }
          }
        }
      }
    },
    rest() {
      // 休息方法
      // 回合判断
      if (this.turns_gone()) {
        // 消耗寿命
        this.life_gone();
        // 回满血
        this.player.hp = this.player.max_hp;
        this.event_msg('rest');
      }
    },
    medbagshow() {
      // 展示丹药背包的方法
    },
  },
};
</script>
